@font-face {
    font-family: 'Hacked';
    src: url(data:font/ttf;charset=utf-8;base64,) format('truetype');
    font-weight: 400;
    font-style: normal;
}

@import "assets/icons";
@import "assets/colors";
@import "assets/vars";
@import "assets/animations";

@import "assets/glitcheds";
